import React from 'react';
import aboutPhoto from '../../assets/images/hospital/about-2.png'

const AboutUs = () => {
    return (
        <div className="grid md:grid-cols-12 grid-cols-1 items-center gap-[30px]">
                    <div className="md:col-span-5">
                        <div className="relative">
                            <img
                                src={aboutPhoto}
                                className="rounded-md"
                                alt=""
                            />
                        </div>
                    </div>
                    <div className="md:col-span-7">
                        <div className="lg:ms-4">
                            <h4 className="mb-6 md:text-3xl text-2xl lg:leading-normal leading-normal font-medium">
                                Hakkmzda
                            </h4>
                            <p className="text-slate-400 max-w-xl">
                                Veteriner Kliniimiz, evcil dostlarnzn salna adanm bir ekip tarafndan iletilmektedir.
                                Salk muayeneleri, alar, acil hizmetler, cerrahi mdahaleler, dental bakm, laboratuvar testleri, beslenme danmanl ve parazit kontrol gibi geni bir hizmet yelpazesi sunuyoruz.

                            </p>
                            <p className="text-slate-400 max-w-xl mt-3">
                                Deneyimli veteriner hekimlerimiz ve modern ekipmanlarmzla evcil hayvanlarnzn salna nem veriyoruz.
                                Kliniimizde, evcil dostlarnzn salna ynelik tm ihtiyalarnz karlamak iin aba gsteriyoruz.
                                Sizin ve dostlarnzn yanndayz.
                            </p>
                            <a
                                href=""
                                className="btn bg-rose-600 hover:bg-rose-700 border-rose-600 hover:border-rose-700 text-white rounded-md mt-3"
                            >
                                Daha Fazla Okuyun <i className="mdi mdi-chevron-right align-middle"/>
                            </a>
                        </div>
                    </div>
        </div>
    );
};

export default AboutUs;